.has-hover-text .hover-text {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
    z-index: 100;
    opacity: 0;
    transition: opacity .2s linear .1s;
    transform: translateY(0)
}

.has-hover-text .hover-text span {
    text-indent: -9999px;
    opacity: .25;
    display: block;
    position: absolute;
    transition: transform cubic-bezier(0.215, .61, .355, 1) .4s;
    height: 50%;
    width: 50%;
    background-size: auto 100%;
    background-repeat: no-repeat
}

.has-hover-text .hover-text span:first-child {
    left: -8px;
    top: 0;
    transform: translateY(50%);
    background-position: top right
}

.has-hover-text .hover-text span:last-child {
    bottom: 0;
    right: -8px;
    background-position: top left;
    transform: translateY(-50%)
}

.has-hover-text .hover-text.hover-text--voir span:first-child {
    width: 58%;
}

.has-hover-text .hover-text.hover-text--voir span:last-child {
